import { Contract, ethers } from 'ethers';
import { useEffect, useState } from 'react';
import abi from '../web3/abi/c250Gold';
import { contractAddress } from '../web3/contracts';

const useBlockchain = () => {
    const [contract, setContract] = useState({});
    const [walletAddress, setWalletAddress] = useState('');
    const [provider, setProvider] = useState();
    const [active, setActive] = useState();

    useEffect(() => {
        let node = 'https://wild-wispy-lake.matic.quiknode.pro/d1b4079c17d824d77beaf104b78d534b1eefd322/';
        if (window.ethereum) node = window.ethereum;
        const fn = async () => {
            const p = new ethers.providers.Web3Provider(node);
            setProvider(p);

            const c = new Contract(contractAddress, abi, p.getSigner());
            setContract(c);

            if (node === window.ethereum) {
                let accounts = await p.send('eth_requestAccounts', []);
                setWalletAddress(accounts[0]);
            }

            setContract(c);
            setActive(true);
        };
        fn();
    }, [setContract, setActive, setProvider]);

    return [contract, walletAddress, active, provider, window.ethereum.networkVersion];
};
export default useBlockchain;
